import React, {useState} from "react";
import { data } from "../utils/data";

export const NetworkTab = () => {
    const [open, setOpen] = useState(false)
    return (
        <div className="">

            <div className="relative flex flex-col">
                <button className="w-full p-4 flex items-center justify-between font-bold tracking-wider border-4 border-transparent actie:border-white">Dropdown</button>
            </div>
            <ul className="focus:outline-none p-0">
                    {
                        data.map((item)=>{
                            console.log(item.icon)
                           return (  

                               <li> 
                                <img 
                                src={item.icon} 
                                alt="Girl in a jacket" 
                                width="500"
                                height="600" />
                               {item.name}  
                               </li>
                           
                           )
                        })
                    }       
                </ul> 


        </div>
    )
}